@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,900&family=Noto+Sans+JP:wght@100;300;400;500&display=swap);
body {
  color: rgb(43, 39, 39);
  line-height: 2rem;
  margin: 0;
  overflow-x: hidden;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    330deg,
    #b2e1fa 0%,
    #83ccf4 25%,
    #58b6e9 50%,
    #0e8dd2 75%,
    #2073d8 100%
  );
  background-size: 400% 400%;
  -webkit-animation: gradscroll 15s linear infinite;
          animation: gradscroll 15s linear infinite;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-family: "Poppins";
  font-weight: 500;
  text-align: center;
  height: auto;
  background-color: #f3ec78;
  background: linear-gradient(
    30deg,
    #f3ec78 0%,
    #f0e959 25%,
    #c7c030 50%,
    #c4bc29 75%,
    #918b1b 100%
  );
  background-size: 400% 400%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  animation: gradscroll 15s ease infinite;
  -webkit-animation: gradscroll 15s ease infinite;
  -moz-animation: gradscroll 15s ease infinite;
}

.forecast-summary {
  border: solid rgba(0, 0, 0, 0);
  border-width: 2px 0;
  border-image: linear-gradient(
    90deg,
    #a280ff 0%,
    #ef89ff 25%,
    #ff7aca 50%,
    #ff67bb 75%,
    #ff5d96 100%
  );
  border-image-slice: 1;
  font-size: 7vw;
  line-height: 3rem;
  padding: 0 2rem;
  text-align: center;
  width: 100%;
}

.forecast-summary__icon {
  margin-top: 0.2em;
  font-size: 4em;
}

.forecase-summary__button {
  border: 0;
  border-radius: 50%;
  background: linear-gradient(
    270deg,
    #a280ff 0%,
    #ef89ff 25%,
    #ff7aca 50%,
    #ff67bb 75%,
    #ff5d96 100%
  );
  background-size: 400% 400%;
  font-family: "Poppins";
  height: 50px;
  width: 50px;
  animation: gradscroll 15s ease infinite;
  -webkit-animation: gradscroll 15s ease infinite;
  -moz-animation: gradscroll 15s ease infinite;
}

.forecase-summary__button:hover {
  animation: gradscroll 1s ease infinite;
  -webkit-animation: gradscroll 1s ease infinite;
  -moz-animation: gradscroll 1s ease infinite;
}

.forecast-summary:nth-of-type(1) {
  border-top: 0;
}
.forecast-summary:nth-of-type(5) {
  border-bottom: 0;
}

.forecast-summary:hover {
  animation: forecast-highlight 4s ease infinite;
  -webkit-animation: forecast-highlight 4s ease infinite;
  -moz-animation: forecast-highlight 4s ease infinite;
}

@media screen and (min-width: 600px) {
  .forecast-summary {
    border-width: 0 0 0 2px;
    font-size: 2vw;
    line-height: 1em;
    margin-top: 1rem;
  }

  .forecast-summary:nth-of-type(1) {
    border-left: 0;
  }
  .forecast-summary:nth-of-type(5) {
    border-right: 0;
  }
}

.forecast-summary:nth-of-type(1) {
  -webkit-animation: fadein 0.6s;
          animation: fadein 0.6s;
}

.forecast-summary:nth-of-type(2) {
  -webkit-animation: fadein 1.2s;
          animation: fadein 1.2s;
}

.forecast-summary:nth-of-type(3) {
  -webkit-animation: fadein 1.8s;
          animation: fadein 1.8s;
}

.forecast-summary:nth-of-type(4) {
  -webkit-animation: fadein 2.4s;
          animation: fadein 2.4s;
}

.forecast-summary:nth-of-type(5) {
  -webkit-animation: fadein 3s;
          animation: fadein 3s;
}

.forecast-summaries {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

@media screen and (min-width: 600px) {
  .forecast-summaries {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}

.forecast-details {
  margin-top: 2rem;
  font-size: 7vw;
  line-height: 3rem;
  text-align: center;
  width: 100%;
  border: 2px solid #a280ff;
  border-image: linear-gradient(
    330deg,
    #a280ff 0%,
    #ef89ff 25%,
    #ff7aca 50%,
    #ff67bb 75%,
    #ff5d96 100%
  );
  border-image-slice: 1;
  background-image: url(/weather-app/static/media/cloud.30e7607a.png);
  background-size: 200% 800%;
  -webkit-animation: fadein 1.5s, gradscroll 300s linear infinite;
          animation: fadein 1.5s, gradscroll 300s linear infinite;
}

@media screen and (min-width: 600px) {
  .forecast-details {
    font-size: 2vw;
    line-height: 2rem;
  }
}

.search-form {
  text-align: center;
}

.search-form__button {
  border: 0;
  border-radius: 50%;
  background-image: linear-gradient(
    90deg,
    #a280ff 0%,
    #ef89ff 25%,
    #ff7aca 50%,
    #ff67bb 75%,
    #ff5d96 100%
  );
  background-size: 400% 400%;
  margin-left: 1rem;
  height: 35px;
  width: 35px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-animation: gradscroll 5s linear infinite;
          animation: gradscroll 5s linear infinite;
}

.search-form__button:hover {
  animation: gradscroll 1s ease infinite;
  -webkit-animation: gradscroll 1s ease infinite;
  -moz-animation: gradscroll 1s ease infinite;
}

.search-form__input {
  border: 0;
  border-radius: 12%;
  font-family: "Poppins";
  height: 30px;
  text-align: center;
}

img {
  height: 20px;
}

@media screen and (min-width: 600px) {
  .forecast-summaries {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}

.credits {
  text-align: center;
}

a {
  color: #eee;
}

.weather-app {
  margin: 0 20px;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes gradscroll {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@-webkit-keyframes gradscroll {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes forecast-highlight {
  to {
    font-size: 4rem;
  }
}
@-webkit-keyframes forecast-highlight {
  to {
    font-size: 4rem;
  }
}

